import CustomDialog from "./CustomDialog";
import { makeStyles } from "@material-ui/styles";
import { useEffect, useState } from "react";
import ColorPicker from "./ColorPicker";
import { TextField } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  row: {
    display: "flex",
    alignItems: "center",
    marginTop: theme.spacing(2),

    "& .label": {
      flex: "110px",
      fontSize: 15,
    },
  },
  textField: {
    "& .MuiOutlinedInput-input": {
      padding: "10px 14px",
    },
  },
}));

const CreateTopic = (props) => {
  const {
    title,
    backgroundP,
    isOpen,
    setIsOpen,
    handleCreateTopic,
    isEdit,
  } = props;
  const [topic, setTopic] = useState("");
  const [background, setBackground] = useState("transparent");

  const createTopic = () => {
    if (background === "transparent" || topic === "") {
      setIsOpen(true);
      alert("Tiu  v mu nn l trng bt buc!");
    } else {
      handleCreateTopic(topic, background);
      setInitialValue();
    }
  };

  const setInitialValue = () => {
    setTopic(title ? title : "");
    setBackground(backgroundP ? backgroundP : "transparent");
  };

  useEffect(() => {
    if (isEdit) {
      setTopic(title);
      setBackground(backgroundP);
    }
  }, [title, backgroundP]);

  return (
    <CustomDialog
      open={isOpen}
      handleClose={setIsOpen}
      title={"To ch "}
      content={
        <CustomDialogContent
          topic={topic}
          setTopic={setTopic}
          setBackground={setBackground}
        />
      }
      cancel={"Hy"}
      agree={"To"}
      eventAgree={createTopic}
      eventCancel={setInitialValue}
      colorTitle={background}
    />
  );
};

export default CreateTopic;

// to phn ni dung ca component CustomDialog
const CustomDialogContent = ({ topic, setTopic, setBackground }) => {
  const classes = useStyles();

  const onChange = (e) => {
    setTopic(e.target.value);
  };

  return (
    <>
      <div className={classes.row}>
        <div className="label">Tn ch :</div>
        <TextField
          className={classes.textField}
          variant="outlined"
          fullWidth
          value={topic}
          onChange={onChange}
        />
      </div>
      <div className={classes.row}>
        <div className="label">Mu nn:</div>
        <ColorPicker setBackground={setBackground} />
      </div>
    </>
  );
};
