import { makeStyles } from "@material-ui/styles";
import { useEffect, useState } from "react";
import { useBeforeunload } from "react-beforeunload";
import Nav from "../components/Nav";
import TopicList from "../components/TopicList";
import VocabularyList from "../components/VocabularyList";
import { firebaseStore } from "../firebase";
import {
  selectUser,
  addDataUser,
  addTopicsList,
} from "../features/user/UserSlice";
import { useSelector, useDispatch } from "react-redux";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  root: {
    background: theme.palette.background.default,
  },
}));

// hm tnh thi gian online ca ngi dng
const calOnlinedTime = (start) => {
  const end = Date.now();
  const minutes = (end - start) / 1000 / 60;
  return parseFloat(minutes.toFixed(1));
};

// hm tr v tun ca ngy trong nm
const getWeek = function (date) {
  const time = new Date(date);
  var onejan = new Date(time.getFullYear(), 0, 1);
  return Math.ceil(((time - onejan) / 86400000 + onejan.getDay() + 1) / 7);
};

// hm so snh 2 ngy vi nhau khi c mili giy
const compareDate = (time1, time2) => {
  const date1 = new Date(time1);
  const date2 = new Date(time2);
  return (
    date1.getFullYear() === date2.getFullYear() &&
    date1.getMonth() === date2.getMonth() &&
    date1.getDate() === date2.getDate()
  );
};

const HomePage = () => {
  const classes = useStyles();
  const [start, setStart] = useState();
  const user = useSelector(selectUser);
  const dispatch = useDispatch();
  const db = firebaseStore.collection("accounts").doc(user.email);

  useBeforeunload((event) => {
    addOnlinedTime(start);
    event.preventDefault();
  });

  // hm thm thi gian online vo firestore
  // s thm khi ngi dng thot trang web
  const addOnlinedTime = async (start = Date.now()) => {
    const minutes = calOnlinedTime(start);
    const dateStart = new Date(start).getDay();
    const index = dateStart - 1 >= 0 ? dateStart - 1 : 6;
    const value = {
      date: start,
      minutes,
    };

    const response = await db.get();
    const fetchData = response.data();
    let onlinedTime = fetchData.onlinedTime;
    const reverse = [...onlinedTime];
    let findNotZero = reverse
      .reverse()
      .find((element) => typeof element !== "number");

    // nu findNotZero c t mt phn t khc 0, s kim tra phn t c cng tun vi phn t cn thm vo mng hay khng
    // nu findNotZero khng c phn t khc 0, push thng trc tip vo mng
    if (findNotZero) {
      // nu ngy c lu trong firestore cng tun vi ngy hin ti th thm data vo mng onlinedTime
      if (getWeek(findNotZero.date) === getWeek(start)) {
        // cng tun
        if (compareDate(findNotZero.date, start)) {
          findNotZero = {
            ...findNotZero,
            minutes: parseFloat((findNotZero.minutes + minutes).toFixed(1)),
          };
          onlinedTime[index] = { ...findNotZero };
        } else {
          // khng cng ngy th add v mng
          onlinedTime[index] = value;
        }
      } else {
        // khng cng tun th s to reset mng v tt c phn t l 0
        onlinedTime.fill(0);
        onlinedTime[index] = value;
      }
    } else {
      // mng cha c lu tr
      onlinedTime[index] = value;
    }

    db.update({
      onlinedTime,
    }).catch((err) =>
      alert("Xy ra li trong qu trnh kt ni vi firestore\n", err)
    );
  };

  useEffect(() => {
    // to d liu cho ngi dng trong csdl
    // nu ti khon c trong csdl t trc s load d liu ln trc tip
    // nu khng c s to d liu mi
    const handleAccount = db.onSnapshot((snap) => {
      if (snap.data()) {
        const data = snap.data();
        dispatch(addDataUser(data));
      } else {
        db.set({
          countVocab: 0,
          countTopic: 0,
          score: 0,
          onlinedTime: [0, 0, 0, 0, 0, 0, 0],
        });
      }
    });

    // ly ch  t csdl v
    const fetchTopicsList = db.collection("topicsList").onSnapshot(
      (snap) => {
        const list = [];

        snap.forEach((doc) => {
          list.push({ ...doc.data(), id: doc.id });
        });
        dispatch(addTopicsList(list));
      },
      (err) => alert("Li trong qu trnh ti ch  t firestore!\n" + err)
    );

    return () => {
      handleAccount();
      fetchTopicsList();
    };
  }, []);

  useEffect(() => {
    setStart(Date.now());

    return () => {
      addOnlinedTime(start);
    };
  }, []);

  return (
    <Router>
      <div className={classes.root}>
        <Nav />

        <Switch>
          <Route exact path="/">
            <TopicList />
          </Route>

          <Route path="/topic/:id">
            <VocabularyList />
          </Route>
        </Switch>
      </div>
    </Router>
  );
};

export default HomePage;
